let HomepageComponent = class HomepageComponent {
    constructor(categoryService, saleBannerService, bookService, cartService) {
        this.categoryService = categoryService;
        this.saleBannerService = saleBannerService;
        this.bookService = bookService;
        this.cartService = cartService;
        this.categories = [];
        this.books = [];
    }
    getCategory() {
        // this.categories = this.categoryService.getCategory();
        this.categoryService.getCategory().subscribe((updatedCategory) => {
            this.categories = updatedCategory.categories;
        });
    }
    getSaleBanner() {
        // this.categories = this.categoryService.getCategory();
        this.saleBannerService.getSaleBanners().subscribe((updatedCategory) => {
            this.banners = updatedCategory.banners;
        });
    }
    getAllBooks() {
        this.bookService.getAllBooks().subscribe(books => {
            this.books = books.value || [];
        });
    }
    ngOnInit() {
        this.getCategory();
        this.getSaleBanner();
        this.getAllBooks();
    }
    countdown(book) {
        countDownSale(book.endSale, "a" + book.id);
    }
    getSaleBooks() {
        let saleBooks = this.books.filter(book => book.isSell);
        return saleBooks;
    }
    addToCart(saleBook) {
        this.cartService.addToCard(Object.assign({}, saleBook, { qty: 1 }));
    }
    getNewProducts() {
        return this.books.filter(book => {
            if (!book.tags || book.tags.length == 0)
                return false;
            console.log(book.tags.indexOf("new") != -1);
            return book.tags.indexOf("new") != -1;
        });
    }
    getBestSellerProducts() {
        return this.books.filter(book => {
            if (!book.tags || book.tags.length == 0)
                return false;
            return book.tags.indexOf("bestseller") != -1;
        });
    }
};
